.info{
    background: #000;
    padding-top: 200px;
    padding-bottom: 100px;
    &__title{
        font-family: "Fugaz One", Arial, Helvetica, sans-serif;
        color: #fff;
        font-size: 56px;
        line-height: 1;
        margin-bottom: 30px;
    }
    .text{
        display: flex;
        margin-bottom: 30px;
        max-width: 600px;
        margin: 0 auto;
    }
    .about{
        color: #fff;
        font-family: "Roboto";
        font-weight: 400;
        font-size: 20px;
        margin-right: 30px;
    }
    .about__text{
        color: #fff;
        font-family: "Roboto";
        font-size: 30px;
    }
    .small__text{
        font-family: "Roboto";
        color: #fff;
        font-size: 20px;
        margin-left: 80px;
        max-width: 600px;
        margin: 0 auto;
    }
}
.collection{
    background: #000;
    padding-top: 50px;
    &__title--one{
        color: #fff;
        font-size: 40px;
        font-family: "Fugaz One";
        line-height: 1.55;
    }
    &__link{
        font-family: "Roboto";
        font-size: 14px;
        color: #fff;
        line-height: 1.55;
        margin-top: 60px;
    }
    &__title{
        display: flex;
        justify-content: space-between;
        max-width: 600px;
        margin: 0 auto;
    }
    &__inner{
        max-width: 600px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
    }
    &__inner-item{
        max-width: 240px;
       
        
    }
    &__inner-title{
        color: #fff;
        font-family: 'FugazOne',Arial,sans-serif;
line-height: 1.55;
font-weight: 400;
text-transform: uppercase;
position: relative;
&::after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgb(158, 156, 156);
    height: 1px;
}
    }
    &__list{
        list-style-type: none;
        max-width: 220px;
        
        height: 240px;
        overflow-y: scroll;
    }
    &__list-item{
        display: flex;
        justify-content: space-between;
        position: relative;
        padding: 30px 30px 30px 0;
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            width: 100%;
            background: rgb(158, 156, 156);
            height: 1px;
        }
    }

    &__list-link{
        color: #e70201;
text-decoration: none;
font-size: 13px;
font-weight: 500;
font-family: 'Roboto';
    }
    .marker{
        color: #ffffff;
font-size: 13px;
font-weight: 500;
font-family: 'Roboto';
margin-right: 50px;
    }
}